import React, { useState } from "react";
import { Row, Col } from "react-grid-system";
import axios from "axios";
import Dialog from '@mui/material/Dialog';
import { DialogActions, DialogContent, DialogContentText } from "@mui/material";
import {url} from "./ConfigUrl";



const UserFile = (props) => {
    const [open, setOpen] = useState(false);

    const deleteClick = (fileid) => {
        let a = { data: { filename: fileid } };
        axios.delete(`${url}/delete`, a
        ).then(
            (res) => {
                console.log("res", res);
                props.value();
            },
            (rej) => {
                console.log("rej", rej);
            }
        )
    }

    return (
        <>
            <Row md={5} style={{ marginBottom: "0%", margin: "5px 5px" }}>

                <Col md={5} style={{ padding: "8px 8px" }}>
                    <p
                        style={{
                            marginLeft: "80px",
                            marginTop: "12px",
                            color: "floralwhite"
                        }}>
                        {props.filename}
                    </p>
                </Col>

                <Col style={{ marginLeft: "3px", padding: "8px 8px" }}>
                    <p style={{
                        color: "floralwhite",
                        marginTop: "7px"
                    }}>
                        {props.size}
                    </p>
                </Col>


                <Col style={{ padding: "8px 8px" }}>
                    <a
                        style={{
                            textDecoration: "none"
                        }}
                        href={`${url} ${props.filename}`} >
                        <button style={{
                            padding: "8px 10px",
                            marginLeft: "2px",
                            border: "none",
                            color: "#f00c32",
                            fontSize: "11px",
                            cursor: "pointer",
                            backgroundColor: "#303032e8"
                        }}>  DOWNLOAD </button>
                    </a>
                </Col>

                <Col style={{ padding: "8px 8px" }}>

                    <button style=
                        {{
                            marginLeft: "2px",
                            color: "#f00c32",
                            border: "none",
                            fontSize: "11px",
                            padding: "9px 13px",
                            cursor: "pointer",
                            backgroundColor: "#303032e8"
                        }}
                        onClick={() => { setOpen(true) }}>
                        DELETE
                    </button>

                    <Dialog open={open} onClose={() => { setOpen(false) }}>
                        <DialogContent>
                            <DialogContentText>
                                Are you sure want to delete this file ?
                            </DialogContentText>
                        </DialogContent>

                        <DialogActions>
                            <button
                                onClick={() => {
                                    deleteClick(props.filename);
                                    setOpen(false);
                                }}> Yes
                            </button>
                            <button
                                onClick={() => { setOpen(false) }}>No
                            </button>
                        </DialogActions>
                    </Dialog>
                    
                </Col>
            </Row>

        </>
    )
}

export default UserFile;
