import React, { useEffect, useState } from "react";
import logo from '../logo.jpg';
import { Container, Row, Col } from "react-grid-system";
import FileContain from './FileContain';
import axios from "axios";
import { url } from "./ConfigUrl";
import { Button } from '@mui/material';



const FileData = () => {

    const [freqData, setFreqData] = useState("");
    const [isHovering, setIsHovering] = useState(false);
    const [date, setDate] = useState("");
    const [time, setTime] = useState("");
    const [dateTime, setDateTime] = useState("");

    const [dataobj, setDataObj] = useState({
        Battery_Voltage: "",
        Frequency: "",
        Temperature: "",
        Odo_Count1: "",
        Odo_Count2: "",
        Odo_Count3: "",
        Set_Frequency: ""
    });

    const { Battery_Voltage, Frequency, Temperature, Odo_Count1, Odo_Count2, Odo_Count3, Set_Frequency } = dataobj;

    useEffect(() => {
        let eventObj = new EventSource(`${url}/event`);
        eventObj.addEventListener("open", () => { console.log("open") });
        eventObj.addEventListener("message", (message) => {
            let obj = JSON.parse(message.data);
            setDataObj({ ...dataobj, ...obj });
        })
        return () => {
            eventObj.close();
        }
    }, []);

    const startFunc = () => {
        axios.get(`${url}/start`).then(
            (res) => {
                console.log("start", res.data);
                if (res.data != "") {
                    alert("SPI communication is already running, please click on stop button to stop the SPI communication");
                }
            },
            (rej) => {
                console.log("rej", rej);
            }
        )
    }

    const stopFunc = () => {
        axios.get(`${url}/stop`).then(
            (res) => {
                console.log("stop", res.data);
                if (res.data != "") {
                    alert("SPI communication is in stop state, please click on start button to  start the SPI communication");
                }
            },
            (rej) => {
                console.log("stop", rej);
            }
        )
    }

    const handleChangeFrequency = (e) => {
        e.preventDefault();
        setFreqData(e.target.value);
    }

    const handleClick = (freqData) => {
        if (freqData >= 50 && freqData <= 1000) {
            axios.get(`${url}/frequency/${freqData}`).then(
                (res) => {
                    console.log("res", res);
                },
                (rej) => {
                    console.log("rej", rej);
                })
        }
        else {
            alert("Please write a value of frequency between 50 to 1000");
        }
    }


    function pad(n) {
        return n < 10 ? '0' + n : n
    }
    const handleTimeDate = () => {
        const d = new Date();

        const date = d.getDate();
        const month = d.getMonth();
        const year = d.getFullYear();

        const ddmmyyyy = year + "-" + pad(month + 1) + "-" + pad(date);
        setDate(ddmmyyyy);

        const hr = d.getHours();
        const mi = d.getMinutes();
        const se = d.getSeconds();

        const hrmise = pad(hr) + ":" + pad(mi) + ":" + pad(se);
        setTime(hrmise);

        setDateTime(ddmmyyyy + " " + hrmise);

        axios.get(`${url}/timer/${dateTime}`).then(
            (res) => { console.log("timer", res) },
            (rej) => { console.log("timer", rej) }
        )
    }

    return (
        <>
            <div style={{
                border: "2px solid", width: "1000px",
                margin: "auto auto", height: "100%",
                backgroundColor: "#393c40"
            }}>

                <div style={{
                    border: "2px solid",
                    height: "60px", width: "900px",
                    margin: "auto auto", display: "flex",
                    backgroundColor: "white"
                }}>
                    <div>
                        <img
                            src={logo} height="50px" width="130px"
                            alt="indianOil"
                            style={{ marginLeft: "10px" }} />
                    </div>

                    <div style={{
                        marginLeft: "600px", marginTop: "2px",
                        marginBottom: "2px", color: "blue"
                    }}>
                        <h3>Whirlybird</h3>
                    </div>
                </div>


                <div>

                    <div
                        style={{
                            height: "40px", width: "800px",
                            margin: "10px 10px", marginLeft: "80px",
                            marginRight: "80px",
                            backgroundColor: "#303032e8",
                            borderLeft: "5px solid #4f71a8"
                        }}>
                        <h3 style={{
                            marginLeft: "3%", marginTop: "3%",
                            paddingTop: "1%", border: "none",
                            color: "floralwhite", borderLeft: "blue"
                        }}> Data </h3>
                    </div>


                    <div>
                        <div>
                            <Button
                                onClick={() => { startFunc() }}
                                style={{
                                    color: "#f00c32", border: "none",
                                    width: "60px", height: "25px",
                                    marginBottom: "20px",
                                    backgroundColor: "#303032e8",
                                    marginLeft: "80px",
                                    fontSize: "10px",
                                    marginRight: "80px"
                                }} variant="contained"> START </Button>

                            <Button
                                onClick={() => { stopFunc() }}
                                style={{
                                    color: "#f00c32", marginLeft: "80px",
                                    marginRight: "80px", border: "none",
                                    width: "60px",
                                    height: "25px",
                                    marginBottom: "20px",
                                    fontSize: "10px",
                                    backgroundColor: "#303032e8"
                                }} variant="contained"> STOP </Button>

                        </div>


                        <Container
                            style={{
                                marginBottom: "10px",
                                marginLeft: "30px"
                            }}>
                            <Row>
                                <Col
                                    style={{ marginRight: "12px" }}>
                                    <li style={{
                                        listStyle: "none", marginLeft: "40px",
                                        color: "floralwhite"
                                    }}>
                                        Battery_Voltage
                                        <input
                                            type="text"
                                            name="Battery_Voltage"
                                            value={Battery_Voltage}
                                            style=
                                            {{
                                                marginLeft: "20px",
                                                width: "50px", height: "20px",
                                                border: "none", backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} /></li>
                                </Col>

                                <Col
                                    style={{ marginLeft: "40px" }}>
                                    <li style={{
                                        listStyle: "none",
                                        color: "floralwhite"
                                    }}>
                                        Frequency
                                        <input
                                            type="text"
                                            name="Frequency"
                                            value={Frequency}
                                            style=
                                            {{
                                                marginLeft: "37px", width: "50px",
                                                height: "20px", border: "none",
                                                backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} /> </li>
                                </Col>

                                <Col
                                    style={{ marginLeft: "10px", marginRight: "50px" }}>
                                    <li style={{
                                        listStyle: "none",
                                        color: "floralwhite"
                                    }}>
                                        Temperature
                                        <input
                                            type="text"
                                            name="Temperature"
                                            value={Temperature}
                                            style=
                                            {{
                                                marginLeft: "22px", width: "50px",
                                                height: "20px", border: "none",
                                                backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} />  </li>
                                </Col>
                            </Row>

                            <Row>
                                <Col style=
                                    {{ marginRight: "10px" }}>
                                    <li style={{
                                        listStyle: "none",
                                        color: "floralwhite", marginLeft: "40px"
                                    }}>
                                        Odo_Count1
                                        <input
                                            type="text"
                                            name="Odo_Count1"
                                            value={Odo_Count1}
                                            style=
                                            {{
                                                marginLeft: "39px", width: "50px", height: "20px", border: "none", backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} /> </li>
                                </Col>

                                <Col
                                    style={{ marginLeft: "40px" }}>
                                    <li style={{
                                        listStyle: "none",
                                        color: "floralwhite"
                                    }}>
                                        Odo_Count2
                                        <input
                                            type="text"
                                            name="Odo_Count2"
                                            value={Odo_Count2}
                                            style=
                                            {{
                                                marginLeft: "20px", width: "50px",
                                                height: "20px", border: "none",
                                                backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} />  </li>
                                </Col>

                                <Col
                                    style={{ marginLeft: "10px", marginRight: "50px" }}>
                                    <li style={{
                                        listStyle: "none",
                                        color: "floralwhite"
                                    }}>
                                        Odo_Count3
                                        <input
                                            type="text"
                                            name=" Odo_Count3"
                                            value={Odo_Count3}
                                            style=
                                            {{
                                                marginLeft: "20px", width: "50px", height: "20px", border: "none", backgroundColor: "#303032e8",
                                                color: "white",
                                                textAlign: "center"
                                            }} /> </li>
                                </Col>
                            </Row>
                        </Container>
                    </div>


                    <div>
                        <div style={{
                            height: "40px", width: "800px",
                            margin: "10px 10px", marginLeft: "80px",
                            marginRight: "80px", backgroundColor: "#303032e8",
                            borderLeft: "5px solid #4f71a8"
                        }}>
                            <h3 style={{
                                marginLeft: "3%", marginTop: "3%",
                                paddingTop: "1%", border: "none",
                                color: "floralwhite"
                            }}> Settings </h3>
                        </div>


                        <div style={{ display: "flex" }}>

                            <div style={{ display: "flex" }}>
                                <li style={{
                                    listStyle: "none", margin: "10px 10px",
                                    marginLeft: "80px", marginRight: "80px",
                                    color: "floralwhite"
                                }}>Set_Frequency
                                </li>

                                <div>
                                    <input type="text"
                                        onMouseEnter={() => { setIsHovering(true) }}
                                        onMouseLeave={() => { setIsHovering(false) }}
                                        defaultValue={Set_Frequency}
                                        onChange={(e) => handleChangeFrequency(e)}

                                        style={{
                                            marginLeft: "80px", marginRight: "80px",
                                            width: "70px", height: "20px", border: "none",
                                            backgroundColor: "#303032e8", marginTop: "10px",
                                            color: "white",
                                            textAlign: "center"
                                        }} />
                                    <div
                                        style={{ color: "white", textAlign: "center" }}>
                                        {isHovering ? "Freq-range (50-1000)" : null}
                                    </div>
                                </div>

                                <Button onClick={() => { handleClick(freqData) }}
                                    style={{
                                        marginLeft: "80px", marginRight: "80px",
                                        color: "#f00c32", width: "50px", height: "20px",
                                        border: "none", backgroundColor: "#303032e8",
                                        marginTop: "10px",
                                        fontSize: "12px",
                                        cursor: "pointer"
                                    }} variant="contained"> SET</Button>
                            </div>

                            <div
                                style={{
                                    color: "white",
                                    marginTop: "6px",
                                    marginLeft: "90px"
                                }}>
                                <button
                                    onClick={handleTimeDate}>
                                    Update-Time
                                </button>
                            </div>
                        </div>


                    </div>

                    <FileContain value={dataobj} />

                </div>
            </div>
        </>
    )
}
export default FileData;