import React, { useState, useEffect } from "react";
import axios from "axios";
import { Container } from "react-grid-system";
import UserFile from './UserFile';
import { confirm } from "react-confirm-box";
import {url} from "./ConfigUrl";



const FileContain = (props) => {
    const [file, setFile] = useState({});

    useEffect(() => {
        getReloadFiles();
    }, [props.value]);

    function getReloadFiles() {
        axios.get(`${url}/file`).then(
            (res) => {
                setFile({ ...res }, res.data);
            },
            (rej) => {
                console.log("rej", rej);
            }
        );
    }

    let a;
    const dataReverse = () => {
        a = [...file.data].reverse();
        setFile({ ...file, data: [...a] });
    }

    const clickDeleteAll = async () => {
        let val = await confirm("Are you sure want to delete all files" + ":" + "?");
        if (val) {
            axios.get(`${url}/deleteall`).then(
                (res) => {
                    console.log("res", res);
                    getReloadFiles();
                },
                (rej) => {
                    console.log("rej", rej);
                }
            );
        }
    }


    return (
        <>
            <div style={{
                display: "flex", height: "40px", width: "800px",
                margin: "10px 10px", marginLeft: "80px",
                marginRight: "80px", backgroundColor: "#303032e8",
                borderLeft: "5px solid #4f71a8", marginBottom: "20px"
            }}>

                <div>
                    <h3 style={{
                        marginLeft: "50%", marginTop: "18%",
                        paddingTop: "1%", color: "floralwhite"
                    }}> Files </h3>
                </div>

                <div style={{
                    marginLeft: "80%",
                    marginTop: "8px"
                }}>
                    <button
                        style={{
                            cursor: "pointer"
                        }}
                        onClick={dataReverse}>
                        ReverseFileData
                    </button>
                </div>
            </div>


            <div
                style={{
                    display: "flex", flexDirection: "Row",
                    marginLeft: "65%", marginBottom: "28px"
                }}>
                <div
                    onClick={clickDeleteAll}
                    style={{ marginTop: "8px" }}>
                    <button
                        style={{
                            cursor: "pointer",
                            marginLeft: "166px"
                        }}>
                        Delete all
                    </button>
                </div>
            </div>

            <Container
                style={{
                    marginBottom: "5px"
                }}>
                {
                    file && file.data ? file.data.map((ele, i) => {
                        return < UserFile key={i} {...ele} value={getReloadFiles} />
                    }) : null
                }
            </Container>
        </>
    )
}
export default FileContain;
